import svg from "./index2.svg"
import * as React from 'react';
import SVG from 'react-inlinesvg';
import styles from 'utils/style.pcss';
import { withRouter } from 'react-router-dom';
import homeStyles from './style.pcss';


const selector = '[id^="Folate"] ellipse';
const dest = '/theories/folate'

export const addClick = (selector, onClick)=>{
  document.querySelector(selector).addEventListener("click", onClick)
  document.querySelector(selector).style = "cursor: pointer;"
}


let img_ele = null,
  x_img_ele = 0,
  y_img_ele = 0;

function start_drag() {
  img_ele = this;
  x_img_ele = window.event.clientX - document.getElementById(homeStyles.dragImage).offsetLeft;
  y_img_ele = window.event.clientY - document.getElementById(homeStyles.dragImage).offsetTop;

}

function stop_drag() {
  img_ele = null;
}

function while_drag() {
  let x_cursor = window.event.clientX;
  if (img_ele !== null) {
    img_ele.style.left = (x_cursor - x_img_ele) + 'px';
    img_ele.style.top = ( window.event.clientY - y_img_ele) + 'px';
  }
}
export default withRouter(class extends React.PureComponent {
  componentDidMount(){
    document.getElementById(homeStyles.dragImage).addEventListener('mousedown', start_drag);
    document.getElementById(homeStyles.container).addEventListener('mousemove', while_drag);
    document.getElementById(homeStyles.container).addEventListener('mouseup', stop_drag);
  }
  onSVGLoad = ()=>{
    addClick(selector, ()=>this.props.history.push(`/apoe4${dest}`))
  }
  
  render(){
    return <div id={homeStyles.container}><div id={homeStyles.dragImage} className={styles.svg}><SVG onLoad={this.onSVGLoad} src={svg} /></div></div>
  }
})


